import { Component, OnInit } from '@angular/core';
import { WeatherService } from './weather.service';
import { timeout } from 'q';
import { error } from 'protractor';
import { Book } from '../model/book';
@Component({
  selector: 'app-weather',
  templateUrl: './weather.component.html',
  styleUrls: ['./weather.component.css'],
  providers: [WeatherService]
})
export class WeatherComponent implements OnInit {
  public data;
  public location = '';
  isloading = false;
  books: Book[];
  message = 'Enter your location?';
  constructor(private weatherService: WeatherService) { }

  ngOnInit() {
    this.getBooks();

  }
  getWeather() {
    this.isloading = true;
    this.getMessage()
    this.weatherService.getTemp(this.location).subscribe(data => {
      this.data = data['main'].temp_max;
      this.isloading = false;
      this.getMessage()
    }, _error => {
      this.message = 'your location is invalid!'
    })

  }
  getBooks() {
    this.weatherService.getBooks().subscribe(data => {
      this.books = data;
      this.books.forEach(element => {
        console.log(element.bookId)
      });
    })
  }
  getMessage() {
    console.log(this.isloading)
    if (this.isloading) {
      this.message = 'Loading....'
    }
    this.message = this.location == '' ? 'Enter your location' : (this.location + ' is now ' + this.data)

  }

}
