import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Book } from '../model/book';

@Injectable({
    providedIn: 'root'
})
export class WeatherService {
    constructor(private http: HttpClient) { }
    public data1;
    getTemp(name: String) {
        const uri = "https://api.openweathermap.org/data/2.5/weather?APPID=008bd33563e0e2a0b10907eacd7099e6&q=" + name;

        var ress = this.http.get(uri);
        console.log(ress)
        return ress;
    }
    getBooks(): Observable<Book[]> {
        const uri = "http://192.168.225.163:8000/books";
        return this.http.get<Book[]>(uri);
    }
}